/*
 *  Rewards Data
 *    Scripts that fetch data from Smile.io and populate rewards page
 *
 *  Version:
 *    1.0.0 - 2021/12/10
 */

import { CustomWindow } from '../../tools/utilities/window';
import { t } from '../../tools/utilities/string-tools';

import { CLASS_INITIALISED, CLASS_HIDDEN } from "../../tools/constants/classes";

declare let window: CustomWindow;

interface SmileCustomer {
  date_of_birth: string | null;
  email: string;
  first_name: string;
  id: number;
  last_name: string;
  points_balance: number
  referral_url: string
  state: string
  updated_at: string
  vip_tier_id: string | null
}

interface FulfilledReward {
  code: string;
  name:string;
  used_at:string;
}

interface PurchasePointsProduct {
  created_at: string;
  fulfilled_reward: FulfilledReward;
}

export interface Smile {
  channel_key: string | null;
  customer: string | number;
  fetchAllPointsProducts(): Promise<PointsProduct[]>;
  fetchAllPointsPurchases(): Promise<PointsProduct[]>;
  fetchCustomer(): SmileCustomer
  purchasePointsProduct(redeemID: number): PurchasePointsProduct
}

interface Reward {
  name: string;
}

interface PointsProduct {
  created_at: string;
  exchange_description: string;
  id: number;
  points_price: number;
  reward: Reward;
  fulfilled_reward: FulfilledReward;
  points_spent:number;
}

interface Coupon {
  fulfilled_reward: FulfilledReward;
}


const SELECTOR_REWARDS_HERO = '[data-rewards-hero]';
const SELECTOR_REWARDS_DETAILS = '[data-rewards-details]';
const SELECTOR_REWARDS_PROGRESS_FILL = '[data-outer-circle]';
const SELECTOR_NEXT_REWARD_NAME = '[data-next-reward]';
const SELECTOR_POINTS_ACCUMULATED = '[data-rewards-points-accumulated]';
const SELECTOR_NEXT_REWARDS_POINTS = '[data-next-rewards-points]';
const SELECTOR_COUPONS_CONTAINER = '[data-rewards-coupons]';
const SELECTOR_REVEAL_BTN = '[data-reward-reveal]';
const SELECTOR_COUPON = '[data-reward-coupon]';
const SELECTOR_COUPON_COPY = '[data-reward-copy]';
const SELECTOR_ACTIVITY_LOG = '[data-rewards-activity]';

const CLASS_SMALLER_TEXT = 'is-unembiggened';
const CLASS_FLIPPED = 'is-flipped';

const ATTR_REDEEM_CODE = 'data-reward-code';



document.querySelectorAll(SELECTOR_REWARDS_HERO).forEach(container => {
  if (container.classList.contains(CLASS_INITIALISED)) return;
  container.classList.add(CLASS_INITIALISED);


  const progressFillElement = container.querySelector<HTMLElement>(SELECTOR_REWARDS_PROGRESS_FILL);
  const rewardNameElement = container.querySelector<HTMLElement>(SELECTOR_NEXT_REWARD_NAME);
  const pointsAccumulatedElement = container.querySelector<HTMLElement>(SELECTOR_POINTS_ACCUMULATED);
  const nextRewardPointsElement = container.querySelector<HTMLElement>(SELECTOR_NEXT_REWARDS_POINTS);
  const rewardsDetails = document.querySelector<HTMLElement>(SELECTOR_REWARDS_DETAILS);

  if (!rewardsDetails) return;
  const couponContainer = rewardsDetails.querySelector<HTMLElement>(SELECTOR_COUPONS_CONTAINER);
  const activityContainer = rewardsDetails.querySelector<HTMLElement>(SELECTOR_ACTIVITY_LOG);
  let Smile: Smile;

  /**
   * 
   * Waits for Smile object before 
   * updating the sections of the page
   */
  const checkSmile = () => {

    // if the customer doesn't exist stop
    if (!window.Smile) return;
    if (!window.Smile.customer) return;

    // clear the interval to stop the function from being called
    clearInterval(smileCheck);

    Smile = window.Smile;

    updateNextReward();
    updateCoupons();
    updateActivityLog();
  }

  /**
   * fetches customer's points and coupon list
   * then calculates the next reward and remaining points
   * then plugs the data into the DOM 
   * 
   */
  const updateNextReward = async () => {
    const customerFetch = Smile.fetchCustomer();
    const pointProductsFetch: Promise<PointsProduct[]> = Smile.fetchAllPointsProducts();
    let promises = await Promise.all([customerFetch, pointProductsFetch]); // fetch at the same time for speed 
    const customer: SmileCustomer = promises[0];
    const pointProducts = promises[1];

    const pointsBalance: number = customer.points_balance;
    const pointsBalanceStr: string = pointsBalance.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","); //add commas to large numbers
    const pointsMessage: string = t('rewards.points_count', { points: pointsBalanceStr });

    let nextReward: PointsProduct | undefined = undefined;
    let pointsPrice: number | null;
    let rewardName: string;
    let exchangeDescription: string;
    let progressToNextReward: number;
    let progressFill: number = 0;
    let full: number = 100;

    // Sort through the array in reverse to find the next reward
    pointProducts.reverse().forEach((prod) => {
      if (prod.points_price > pointsBalance) {
        nextReward = prod;
      }
    });

    if (nextReward){
      //@ts-ignore
      pointsPrice = nextReward.points_price;
      //@ts-ignore
      rewardName = nextReward.reward.name;
      //@ts-ignore
      exchangeDescription = nextReward.exchange_description;
      if (!pointsPrice) return;
      progressToNextReward = Math.round(100 * (pointsBalance / pointsPrice));
    } else {
      exchangeDescription = '';
      progressToNextReward = 100;
      rewardName = t('rewards.congrats');
    }



    if (
      !rewardNameElement ||
      !pointsAccumulatedElement ||
      !nextRewardPointsElement ||
      !progressFillElement
    ) return;


    /**
     * -- Progress Animation --
      * Because the percentage goes counterclockwise 
      * and the conic gradient goes clockwise
      * we'll start at 100 and substract percentage to next reward
     */
    let fillProgressAnimation = window.setInterval(function () {
      progressFillElement.style.background = `conic-gradient(#BCB0AC ${full}%,#b96955 0)`;
      full--;
      if (++progressFill > progressToNextReward) window.clearInterval(fillProgressAnimation);
    }, 10);

    /** 
     * Hide elements that will have data plugged in
     * then plug in the data
     * then unhide, creating opacity transition effect 
     */
    rewardNameElement.classList.add(CLASS_HIDDEN);
    pointsAccumulatedElement.classList.add(CLASS_HIDDEN);
    nextRewardPointsElement.classList.add(CLASS_HIDDEN);
    setTimeout(function () {
      rewardNameElement.innerHTML = rewardName;
      pointsAccumulatedElement.innerHTML = pointsMessage;
      nextRewardPointsElement.innerHTML = exchangeDescription;

      rewardNameElement.classList.remove(CLASS_HIDDEN);
      pointsAccumulatedElement.classList.remove(CLASS_HIDDEN);
      nextRewardPointsElement.classList.remove(CLASS_HIDDEN);

      if (nextReward) rewardNameElement.classList.remove(CLASS_SMALLER_TEXT);

    }, 400);
  }

  /**
   * simultaneously fetches points products and points purchases
   * Loops through all coupons and sorts them into available (less than points balance)
   * and unavailable {more than points balance}
   * Prints all coupons
   * Then calls appendCoupons
   * @param idx optional - number, index of redeemed coupon within container
   * 
   */
  const updateCoupons = async (idx: number | null = null) => {

    const customerFetch = Smile.fetchCustomer();
    const pointProductsFetch = Smile.fetchAllPointsProducts();
    const redeemedCouponsFetch = Smile.fetchAllPointsPurchases();
    let promises = await Promise.all([customerFetch, pointProductsFetch, redeemedCouponsFetch]);
    const customer: SmileCustomer = promises[0];
    const pointProducts: PointsProduct[] = promises[1];
    const redeemedCouponsArr = promises[2];
    const pointsBalance = customer.points_balance;
    let availableCouponArr: PointsProduct[] = [];
    let unavailableCouponArr: PointsProduct[] = [];

    // Find rewards that cost less than point balance
    pointProducts.forEach((prod: PointsProduct) => {
      if (prod.points_price <= pointsBalance) {
        availableCouponArr.push(prod);
      }
    });

    // Create array of available rewards IDs
    const pointsIDs: number[] = availableCouponArr.map((pointProduct: PointsProduct) => pointProduct.id);

    // Create array of unavailable rewards
    pointProducts.forEach((product: PointsProduct) => {
      if (!pointsIDs.includes(product.id)) {
        unavailableCouponArr.push(product);
      }
    });

    // Print all coupons
    const availableCoupons: string = availableCouponsPrint(availableCouponArr);
    const upcomingCoupons: string = unavailableCouponsPrint(unavailableCouponArr);
    const redeemedCoupons: string = redeemedCouponsPrint(redeemedCouponsArr);

    // Append coupons to contiainer
    appendCoupons(availableCoupons, upcomingCoupons, redeemedCoupons, idx);
  }

  /**
   * Fetches activity data
   * Converts data to readable strings
   * Create HTML for activity
   * Inserts HTML into activity log container
   */
  const updateActivityLog = async () => {
    const rewardFulfillmentsArr: PointsProduct[] = await Smile.fetchAllPointsPurchases();
    let html = '';
    if (!activityContainer) return;

    // If no activity, display message
    if (!rewardFulfillmentsArr.length) {
      activityContainer.innerHTML = 
      `<p class="c-rewards-details__activity-message">
        ${t('rewards.no_activity')}
      </p>`;
    
    } else {
      rewardFulfillmentsArr.forEach(item => {
        // Create activity string
        let activityName = t('rewards.activity', { reward: item.fulfilled_reward.name });
        // Create date string
        let months = ["", "Jan", "Feb", "Mar", "Apr", "May", "June", "July", "Aug", "Sep", "Oct", "Nov", "Dec"];
        let dateArr = item.created_at.slice(0, 10).split('-');
        let monthIndex = parseInt(dateArr[1]);
        let monthStr = months[monthIndex];
        let dateStr = `${monthStr} ${dateArr[2]}, ${dateArr[0]}`;
        // Create points string
        let points = item.points_spent.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","); // add comma(s) to number
        let pointsStr = t('rewards.spent', { points: points });


        html += `
        <div class="c-rewards-details__activity">
          <p class="c-rewards-details__activity-name">
            ${activityName}
          </p>
          <p class="c-rewards-details__activity-date">
            ${dateStr}
          </p>
          <p class="c-rewards-details__activity-points">
            -${pointsStr}
          </p>      
        </div>
        `
      });

      // Append html
      activityContainer.innerHTML = html;
    }

  }

  /**
   * loops through array and creates html based on whether the coupon has been used
   * 
   * @param couponsArr - array of redeemed coupons
   * @returns html string of redeemed coupons
   */
  const redeemedCouponsPrint = (couponsArr: Coupon[]) => {
    let html = '';

    for (const coupon of couponsArr) {

      const couponName = coupon.fulfilled_reward.name;
      const couponUsed = coupon.fulfilled_reward.used_at
      const couponMessage = t('rewards.copy_code');
      const couponRedeemed = t('rewards.redeemed');

      if (!couponUsed) {
        html += `
          <div class="o-coupon is-available is-flipped" data-reward-coupon>       
            <div class="o-coupon__back-face">
              <h6 class="o-coupon__pretitle">${couponName}</h6>
              <h3 class="o-coupon__name">${couponMessage}</h3>
              <button class="o-coupon__back-btn o-btn" data-reward-copy>
                ${coupon.fulfilled_reward.code}
              </button>
            </div>
          </div>
          `
      } else {
        html += `
          <div class="o-coupon is-redeemed" data-reward-coupon>              
            <h6 class="o-coupon__pretitle">${couponName}</h6>
            <h3 class="o-coupon__name">${couponName}</h3>
            <button class="o-coupon__btn o-btn">
              ${couponRedeemed}
            </button>
          </div>
        `
      }
    }
    return html;
  }

  /**
   * creates string of html for available coupons
   * @param couponsArr - array of coupons that cost less than points balance
   * @returns html string of available coupons
   */
  const availableCouponsPrint = (couponsArr: PointsProduct[]) => {
    let html = '';

    for (const coupon of couponsArr.reverse()) {
      const couponName = coupon.reward.name;
      const couponID = coupon.id;
      const couponPts = coupon.exchange_description;
      const couponMessage = t('rewards.copy_code');
      const reveal = t('rewards.reveal');
      html += `
      <div class="o-coupon is-available" data-reward-coupon>
        <div class="o-coupon__front-face">
          <h6 class="o-coupon__pretitle">${couponPts}</h6>
          <h3 class="o-coupon__name">${couponName}</h3>
          <button class="o-coupon__front-btn o-btn" data-reward-reveal data-reward-code="${couponID}">${reveal}</button>
        </div>
        <div class="o-coupon__back-face">
          <h6 class="o-coupon__pretitle">${couponName}</h6>
          <h3 class="o-coupon__name">${couponMessage}</h3>
          <button class="o-coupon__back-btn o-btn" data-reward-copy></button>
        </div>
      </div>
      `
    }
    return html;
  }

  /**
   * creates string of html for unavailable coupons
   * @param couponsArr  - array of coupons that cost more than points balance   * 
   * @returns htmtl string of unavailable coupons
   */
  const unavailableCouponsPrint = (couponsArr: PointsProduct[]) => {

    let html = '';
    for (const coupon of couponsArr) {
      const couponPoints = coupon.exchange_description;
      const couponName = coupon.reward.name;
      const couponUnlock = t('rewards.unlock');
      html += `
        <div class="o-coupon is-unavailable" data-reward-coupon>        
          <h6 class="o-coupon__pretitle">${couponPoints}</h6>
          <h3 class="o-coupon__name">${couponName}</h3>
          <button class="o-coupon__btn o-btn">${couponUnlock}</button>
        </div>
        `
    }
    return html;
  }

  /** 
   * Appends HTML string into coupon container
   * If called after a redeem event, reorder array of coupons so the clicked coupon stays in same index
   * Add event listeners to reveal and copy buttons
   * 
   * @param availableCoupons - html string of available coupons
   * @param upcomingCoupons  - html string of unavailable coupons
   * @param redeemedCoupons  - html string of redeemed coupons
   * @param idx - optional, used when appendCoupons is called after a coupon has been redeemed
   * to keep its place in order
   *
   */
  const appendCoupons = (availableCoupons: string, upcomingCoupons: string, redeemedCoupons: string, idx: number | null = null) => {
    if (!couponContainer) return;

    if (!idx) {
      couponContainer.innerHTML = redeemedCoupons + availableCoupons + upcomingCoupons;
    } else {
      // Create new element that will not be appended anywhere
      const couponInnerContainer = document.createElement('div');

      // Insert html strings
      couponInnerContainer.innerHTML = redeemedCoupons + availableCoupons + upcomingCoupons;

      // Create array of HTML elements from the strings just inserted
      const couponArr: HTMLElement[] | any[] = Array.from(couponInnerContainer.querySelectorAll(SELECTOR_COUPON));

      // Clicked coupon will be at the beginning of the array, so remove first item from array
      let clickedCoupon: HTMLElement = couponArr.shift();

      // And insert to its original index
      couponArr.splice(idx, 0, clickedCoupon);

      // Turn elements back into strings
      const strings = couponArr.map(coupon => coupon.outerHTML);

      // Join them into one string
      const html = strings.join('');

      // Inject into DOM
      couponContainer.innerHTML = html;
    }

    const revealButtons = couponContainer.querySelectorAll<HTMLElement>(SELECTOR_REVEAL_BTN);
    const copyButtons = couponContainer.querySelectorAll<HTMLElement>(SELECTOR_COUPON_COPY);

    // Add event listener to reveal buttons
    revealButtons.forEach(btn => btn.addEventListener('click', () => {
      const redeemId = btn.getAttribute(ATTR_REDEEM_CODE);
      const couponWrapper = btn.closest<HTMLElement>(SELECTOR_COUPON);
      if (!couponWrapper || !redeemId) return;
      redeemCoupon(redeemId, couponWrapper);
    }));

    // Add event listener to reveal buttons
    copyButtons.forEach(btn => btn.addEventListener('click', () => copyCode(btn)));
  }

  /** 
   * fetches reward
   * adds class to coupon 
   * inserts coupon code into the button
   * Updates points, next reward, coupons, and activity
   * 
   * @param id - string of redeem code
   * @param couponWrapper - coupon element
   */
  const redeemCoupon = async (id: string, couponWrapper: HTMLElement) => {
    if (!couponContainer) return;
    const couponsArr = Array.from(couponContainer.querySelectorAll<HTMLElement>(SELECTOR_COUPON));
    const idxClickedCoupon: number = couponsArr.indexOf(couponWrapper);
    const redeemId = parseInt(id);
    const rewardObj = await Smile.purchasePointsProduct(redeemId);
    const couponCode = rewardObj.fulfilled_reward.code;
    const copyButton = couponWrapper.querySelector(SELECTOR_COUPON_COPY);

    if (!couponWrapper || !copyButton) return;

    copyButton.innerHTML = couponCode;
    couponWrapper.classList.add(CLASS_FLIPPED);


    updateNextReward();
    updateCoupons(idxClickedCoupon);
    updateActivityLog();
  }

  /**
   * Copies reward code to clipboard
   * @param button HTMLElement of button containing code
   */
  const copyCode = (button: HTMLElement) => {
    let code = button.innerText;
    const element = document.createElement('textarea');
    element.value = code;
    document.body.appendChild(element);
    element.select();
    document.execCommand('copy');
    document.body.removeChild(element);
    setTimeout(function () { button.innerText = t('rewards.copied'); }, 500);
    setTimeout(function () { button.innerText = code; }, 2000);
  }


  const smileCheck = setInterval(e => checkSmile(), 100);
});