/*
 *  Products Carousel
 *    Scripts for the products carousel
 *
 *  Version:
 *    1.0.0 - 2021/10/10
 */

import KeenSlider from "keen-slider";
import { initCarousel, CarouselOptions, initCarouselParams } from "../../objects/carousel";
import { responsiveMediaAbove, responsiveMediaBelow, ResponsiveOptions, responsiveQuery } from "../../settings/responsive";
import { CLASS_INITIALISED } from "../../tools/constants/classes";

// Selectors
const SELECTOR_SECTION = '[data-products-carousel-section]';
const SELECTOR_CAROUSEL = '[data-products-carousel]';
const SELECTOR_SLIDE = '[data-products-carousel-slide]';
const SELECTOR_NAVIGATION = '[data-carousel-navigation]';

// Carousel options
const options:CarouselOptions = {
  slidesPerView: 1.337,
  loop: true,
  centered: true,
  breakpoints: {
    ['(min-width: 550px)']: {
      slidesPerView: 1.8
    },
    [responsiveQuery('SMALL')]: {
      slidesPerView: 3.2
    },
    [responsiveQuery('MEDIUM')]: {
      slidesPerView: 4,
      centered: false
    }
  }
}

// Initialise sections
document.querySelectorAll<HTMLElement>(SELECTOR_SECTION).forEach(section => {
  if(section.classList.contains(CLASS_INITIALISED)) return;
  section.classList.add(CLASS_INITIALISED);

  const carouselElement = section.querySelector<HTMLElement>(SELECTOR_CAROUSEL);
  if(!carouselElement) return;

  const slides = carouselElement.querySelectorAll<HTMLElement>(SELECTOR_SLIDE);
  if(!slides) return;

  // If there are less then 4 slides add a numbered class
  if(slides.length <= 4) {
    if(slides.length == 1) {
      carouselElement.classList.add(`has-${slides.length}-product`);
    } else {
      carouselElement.classList.add(`has-${slides.length}-products`);
    }
  }

  // If there is only 1 slide then we don't need the carousel
  if(slides.length <= 1) return;

  // Different breakpoint options depending on slides number
  switch (slides.length) {
    case 2:
      options.breakpoints = {
        [responsiveQuery('SMALL')]: {
          created: (slider: KeenSlider) => slider.destroy()
        }
      };
      break;
    case 3:
    case 4:
      options.breakpoints = {
        ['(min-width: 550px)']: {
          slidesPerView: 1.8
        },
        [responsiveQuery('SMALL')]: {
          slidesPerView: 2.4
        },
        [responsiveQuery('MEDIUM')]: {
          created: (slider: KeenSlider) => slider.destroy()
        }
      }
      break;
    default:
      break;
  }

  const params:initCarouselParams = { container: carouselElement, options };
  const navigationContainer = section.querySelector<HTMLElement>(SELECTOR_NAVIGATION);
  if(navigationContainer) params.navigationContainer = navigationContainer;

  const carousel = initCarousel(params);

  /**
   * Destroys or refreshes carousel based on number of products
   * for example if there are only 2 products, destroy the carousel above the
   * small breakpoint
  */
  const onResize = () => {
    if(!carousel) return;
    const slider = carousel.slider;

    // Destory/Refresh based on bow many products are in carousel
    let destroySize: ResponsiveOptions;
    switch (slides.length) {
      case 2:
        destroySize = 'SMALL';
        break;
      case 3:
      case 4:
        destroySize = 'MEDIUM';
        break;
      default:
        return;
    }

    if(responsiveMediaAbove(destroySize) && carouselElement.classList.contains(CLASS_INITIALISED)) {
      slider.destroy();
    } else if (responsiveMediaBelow(destroySize) && !carouselElement.classList.contains(CLASS_INITIALISED)) {
      slider.refresh();
    }
  }

  window.addEventListener('resize', onResize);
  window.addEventListener('load', onResize);
});