/**
 * Truncate Words
 * @param words The string of words to truncate
 * @param limit The number of words to limit the truncation to
 * @param after A optional string to place at the end of the truncation
 */
export const truncateWords = (words:string, limit:number, after?:string) => {
  if(!words || !limit) return;

  // Clean words
  words = words.trim();

  // Break words by spaces
  let wordsArray = words.split(' ');

  // Get original words count
  let originalCount = wordsArray.length;

  // Truncate words array
  wordsArray = wordsArray.slice(0, limit);

  // Get the truncated count
  let truncatedCount = wordsArray.length;

  // Only show the after string if the words string was truncated
  let showAfter = after && (truncatedCount < originalCount);

  // Put it back together again
  words = `${wordsArray.join(' ')}${showAfter ? after : ''}`;

  return words
}

/**
 *  Handlize
 *    Takes in a string and converts it to a Shopify formatted handle
 *
 * @param {string} s String to handlize
 * @returns {string} Handle
 */
export const handlize = (s:string) => {
  let str = s.toLowerCase();
  ['"', "'", "\\", "(", ")", "[", "]"].forEach(e => str = str.replace(e, ''));

  str = str.replace(/\W+/g, "-");

  while(str.startsWith('-')) str = str.substr(1);
  while(str.endsWith('-')) str = str.slice(0, -1);

  return str;
}

/**
 *  Liquid
 *    Takes in a string containing liquid-like variables e.g. {{ test }} and
 *    replaces them with the variables provided
 *
 * @param {string} str The liquid string
 * @param {object} vars The variables to replace
 * @returns {string} The formatted string
 */
 export const liquid = (str:string, vars:{[key:string]:string}={}) => {
  let keys = Object.keys(vars);
  let keysLower = keys.map(k => k.toLowerCase());

  return str.replace(/({)?{{[^{}]*}}(?!})/g, function($0, $1){
    let x = $0.replace(/[\{|\s|\}]/g, '');
    if(!x) return $0;

    //Lowercaseify
    x = x.toLowerCase();

    let keyIndex = keysLower.findIndex(k => k == x);
    if(keyIndex === -1) return $0;
    return vars[keys[keyIndex]];
  });
}

export const t = (key:string, variables:{[key:string]:string}={}) => {
  let str;

  //Try find
  //@ts-ignore
  if(window && window['Language'] && window['Language'].strings) {
    //@ts-ignore
    str = window['Language'].strings[key];
  }
  if(!str || !str.length) return `translation missing: ${key}`;
  return liquid(str, variables);
}